@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* bu k覺sm覺 kald覺r覺rsan tablolar eski haline gelir */

@layer components{
  .input{
    @apply h-10 rounded px-4 outline-none w-full text-black border border-zinc-400 text-sm focus:border-black
}
.textarea{
    @apply px-1 pt-1 text-sm outline-none border border-zinc-400 focus:border-black resize-none w-full
}
.button{
    @apply bg-[#E19930] mt-2 text-sm rounded h-10  text-white font-semibold disabled:opacity-50
}
.title{
    @apply text-lg font-semibold text-center
}
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ant-btn-primary {
  background-color: #1677ff;
}

.bg-gradient{
  background: linear-gradient(90deg, rgba(45, 168, 207, 1) 0%, rgba(59, 95, 246, 1) 100%);
}

